import axios from './axios'

export const getMenu = (param) => {
    return axios.request({
        url: '/menu/list',
        method: 'post',
        data: param
    })
}
export const getHomeData = () => {
    return axios.request({
        url: '/homeData',
        method: 'get'
    })
}
export const getUserData = () => {
    return axios.request({
        url: '/user/get',
        method: 'get',
    })
}
export const addUserData = (param) => {
    return axios.request({
        url: '/user/add',
        method: 'post',
        data: param
    })
}
export const editUserData = (param) => {
    return axios.request({
        url: '/user/edit',
        method: 'post',
        data: param
    })
}
export const delUserData = (params) => {
    return axios.request({
        url: '/user/del',
        method: 'post',
        data: params
    })
}
export const searchKeyword = (params) => {
    return axios.request({
        url: '/user/search',
        method: 'get',
        params: params
    })
}
export const loginTest = (params) => {
    return axios.request({
        url: '/user/login',
        method: 'post',
        data: params
    })
}